<template>
    <div>
        <TopNav mdl="coupon" select="0"></TopNav>
        <div class="wyu-tips wyu-tips-warning">
            <p> 1） 这是管理优惠券管理 </p>
        </div>
        <div class="wyu-main">
            <div class="wyu-block" v-loading="loading">
                <el-form size="mini" v-model="searchForm" class="search-form" :inline="true">
                    <el-form-item label="优惠券名称">
                        <el-input v-model="searchForm.keyword" placeholder="输入关键字"></el-input>
                    </el-form-item>

                    <el-form-item label="类型">
                        <el-select v-model="searchForm.receivetype"   placeholder="请选择">
                            <el-option label="请选择" :value="-999"></el-option>
                            <el-option v-for="item in datas.couponUseType" :label="item.value" :value="item.key"></el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="使用范围">
                        <el-select v-model="searchForm.usearea"   placeholder="请选择">
                            <el-option label="请选择" :value="-999"></el-option>
                            <el-option v-for="item in datas.useArea" :label="item.value" :value="item.key"></el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="发放类型">
                        <el-select v-model="searchForm.type"   placeholder="请选择">
                            <el-option label="请选择" :value="-999"></el-option>
                            <el-option v-for="item in datas.couponType" :label="item.value" :value="item.key"></el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="是否上架">
                        <el-select v-model="searchForm.is_online"   placeholder="请选择">
                            <el-option label="请选择" :value="-999"></el-option>
                            <el-option label="已下架" :value="0"></el-option>
                            <el-option label="上架中" :value="1"></el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="领取时间">
                        <date-between  v-model="searchForm.send_date"></date-between>
                    </el-form-item>

                    <el-form-item label="有效时间">
                        <date-between  v-model="searchForm.expire_date"></date-between>
                    </el-form-item>

                    <div style="padding-left:80px;">
                        <el-button icon="el-icon-search" @click="getLists(1)" size="mini" type="primary">搜索</el-button>
                        <el-button @click="searchForm.keyword='',searchForm.usearea=-999,searchForm.type=-999,
                        searchForm.is_online=-999,searchForm.send_date='',searchForm.expire_date='',getLists(1)" size="mini" type="text">取消搜索</el-button>
                        <router-link to="/marketing/coupon/create">
                            <el-button size="mini" type="danger">新增优惠券</el-button>
                        </router-link>
                    </div>
                </el-form>
                <el-table :data="datas.list" style="width: 100%;margin-top:20px;" @selection-change="handleSelectionChange">
                    <el-table-column type="selection" width="55"></el-table-column>

                    <el-table-column label="ID" prop="coupon_id" width="80"></el-table-column>

                    <el-table-column label="优惠券">
                        <template slot-scope="scope">
                            <div class="ellipsis-02">{{scope.row.name}}</div>
                        </template>
                    </el-table-column>

                    <el-table-column label="抵用金额" prop="money" ></el-table-column>

                    <el-table-column label="消费需满足" prop="condition" ></el-table-column>
+
                    <el-table-column label="类型" >
                        <template slot-scope="scope">
                            {{scope.row.type_mean}}
                        </template>
                    </el-table-column>

                    <el-table-column label="获取类型" >
                        <template slot-scope="scope">
                            {{scope.row.get_type_mean}}
                        </template>
                    </el-table-column>

                    <el-table-column label="使用范围">
                        <template slot-scope="scope">
                            <p>{{scope.row.userarea_mean}}</p>
                            <p v-if="scope.row.userarea > 0">{{scope.row.cat_name}}</p>
                        </template>
                    </el-table-column>

                    <el-table-column label="总数/领取/使用" >
                        <template slot-scope="scope">
                            总数：{{scope.row.create_num}}
                            领取：{{scope.row.send_num}}
                            使用：{{scope.row.use_num}}
                        </template>
                    </el-table-column>


                    <el-table-column label="是否上架" >
                        <template slot-scope="scope">
                            <el-switch :value="scope.row.is_online" :inactive-value="0" :active-value="1" @change="setOnline(scope,scope.row.is_online)"></el-switch>
                            &nbsp;
                        </template>
                    </el-table-column>

                    <el-table-column label="领取时间" width="200">
                        <template slot-scope="scope">
                            <p>从：{{scope.row.send_start_time}}</p>
                            <p>至：{{scope.row.send_end_time}}</p>
                        </template>
                    </el-table-column>

                    <el-table-column label="有效期" width="200">
                        <template slot-scope="scope">
                            <p>从：{{scope.row.expire_start_time}}</p>
                            <p>至：{{scope.row.expire_end_time}}</p>
                        </template>
                    </el-table-column>


                    <el-table-column label="操作" width="200">
                        <template slot-scope="scope">
                            <router-link :to="'/marketing/coupon/edit/' + scope.row.coupon_id">
                                <el-button size="mini" type="text">编辑</el-button>
                            </router-link>
                            <el-popover placement="top" width="160" v-model="scope.row.isDel">
                                <p>您确定要删除么</p>
                                <div style="text-align: right; margin: 0">
                                    <el-button  type="text" @click="scope.row.isDel = false">取消</el-button>
                                    <el-button type="text"  @click="delAct(scope.row.couponId)">确定</el-button>
                                </div>
                                <el-button type="text" style="margin-left:10px;" slot="reference">删除</el-button>
                            </el-popover>
                        </template>
                    </el-table-column>
                </el-table>

                <div class="page">
                    <div class="page-tool">
                        <el-button  size="mini" type="primary" @click="setOnline('',1)">批量上架 </el-button>
                        <el-button  size="mini" type="danger" @click="setOnline('',0)">批量下架 </el-button>
                    </div>
                    <div class="page-mian">
                        <el-pagination @current-change="getLists" :page-size="datas.limit"
                                       layout="total, prev, pager, next" :total="datas.count">
                        </el-pagination>
                    </div>
                </div>

            </div>
        </div>


    </div>
</template>
<script>
    import TopNav from "@/components/TopNav";
    import * as api from "@/api";
    import DateBetween from "@/components/DateBetween";
    import ProductCategory from "@/pages/product/components/ProductCategory";


    export default {
        name: "couponIndex",
        components: {ProductCategory, TopNav,DateBetween},
        data: function() {
            return {
                subLoading: false,
                dialog: false,
                keyword:'',
                loading:false,
                datas: [],
                searchForm: {
                    keyword: '',
                    type: -999,
                    usearea: -999,
                    is_online: -999,
                    send_date: '',
                    expire_date: '',
                },
                form: {},
                couponIds: [],
                coupon_id: 0,

            };
        },
        created() {
            this.getLists();
        },
        methods: {
            handleSelectionChange() {
                let Ids = [];
                val.forEach(val => {
                    Ids.push(val.coupon_id);
                });
                this.couponIds = Ids;
                console.log(this.couponIds);
            },


            setVip: function(scope){
                this.loading = true;
                let is_vip = 0;
                if (scope.row.is_vip == 0) {
                    is_vip = 1;
                }
                scope.row.is_vip = is_vip;
                api.post('shop/admin.marketing.coupon/setVip',{
                    appToken: this.$store.getters.appToken,
                    coupon_id: scope.row.coupon_id,
                    is_vip: is_vip
                }).then(()=>{
                    this.$message({
                        message:'操作成功',
                        type:'success',
                    });
                    //this.getLists();
                    this.loading = false;
                }).catch(() => {
                    this.loading = false;
                });
            },


            setCurrency: function (scope) {
                this.loading = true;
                let is_currency = 0;
                if (scope.row.is_currency == 0) {
                    is_currency = 1;
                }
                scope.row.is_currency = is_currency;
                api.post('shop/admin.marketing.coupon/setCurrency',{
                    appToken: this.$store.getters.appToken,
                    coupon_id: scope.row.coupon_id,
                    is_currency: is_currency
                }).then(()=>{
                    this.$message({
                        message:'操作成功',
                        type:'success',
                    });
                    this.getLists();
                    this.loading = false;
                }).catch(() => {
                    this.loading = false;
                });
            },


            setOnline: function (scope='',is_online=0) {
                this.loading = true;
                if (scope != '') {
                    if (scope.row.is_online == 0) {
                        is_online = 1;
                    } else {
                        is_online = 0;
                    }
                    scope.row.is_online = is_online;
                }

                api.post('shop/admin.marketing.coupon/setOnline',{
                    appToken: this.$store.getters.appToken,
                    coupon_id: scope.row.coupon_id,
                    coupon_Ids: this.coupon_Ids,
                    is_online: is_online
                }).then(() =>{
                    this.$message({
                        message:'操作成功',
                        type:'success',
                    });
                    this.getLists();
                    this.loading = false;
                }).catch(() => {
                    this.loading = false;
                });
            },


            getCoupon(scope) {
                this.form = scope.row;
            },


            editCoupon(scope) {
                this.loading = true;
                let is_online = 0;
                if (scope.row.is_online == 0) {
                    is_online = 1;
                }
                scope.row.is_online = is_online;
                api.post('shop/admin.marketing.coupon/setOnline',{
                    appToken: this.$store.getters.appToken,
                    coupon_id: scope.row.coupon_id,
                    is_online: is_online
                }).then(res=>{
                    this.$message({
                        message:'操作成功',
                        type:'success',
                    });
                    //this.getLists();
                    this.loading = false;
                }).catch(() => {
                    this.loading = false;
                });
            },


            delAct:function(couponId){
                this.loading = true;
                var that = this;
                api.post('app/coupon.coupon/del',{
                    appToken: that.$store.getters.appToken,
                    couponId:couponId
                }).then(res=>{
                    that.loading = false;
                    that.$message({
                        message:'删除成功',
                        type:'success',
                    });
                    that.getLists();

                }).catch(() => {
                    that.loading = false;
                });
            },
            setPage:function(res){
                this.page = res;
                this.getLists();
            },

            getLists:function(){
                this.loading = true;
                var that = this;
                api.post('shop/admin.marketing.coupon/index',{
                    appToken: that.$store.getters.appToken,
                    keyword:that.searchForm.keyword,
                    is_currency:that.searchForm.is_currency,
                    is_vip:that.searchForm.is_vip,
                    send_start_time:that.searchForm.send_date[0],
                    send_end_time:that.searchForm.send_date[1],
                    expire_start_time:that.searchForm.expire_date[0],
                    expire_end_time:that.searchForm.expire_date[1],
                    page:that.page,
                    limit:that.limit,
                }).then(res=>{
                    that.loading = false;
                    that.datas = res;
                }).catch(() => {
                    that.loading = false;
                });
            },
            onSearch:function(){
                this.page = 1;
                this.getLists();
            },
        }
    };
</script>
